/*===================================================================
  Используемый стиль методологии BEM-naming
=====================================================================
Стиль без подчеркиваний blockName-elemName--modName--modVal
— Для записи имен используется CamelCase.
— Имя элемента отделяется от имени блока с помощью одного дефиса (-).
— Для отделения модификатора используется два дефиса (--).
— Значение модификатора отделяется от его имени с помощью двух дефисов (--).
===================================================================*/
/*===================================================================
  TYPOGRAPHY
=====================================================================*/
/* Fluid Text with fallback
* 1rem - font size
* .25 (4px) - max font size => 16px+4px=20px
* 20rem (320px) - min width
* 80 (1280px) - max width.
*/
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 8px;
  line-height: 1.25;
}
h1 {
  font-size: 36px;
  font-size: calc(2.25rem + 0.5 * ((100vw - 20rem) / 80));
}
h2 {
  font-size: 30px;
  font-size: calc(1.875rem + 0.5 * ((100vw - 20rem) / 80));
}
h3 {
  font-size: 24px;
  font-size: calc(1.5rem + 0.5 * ((100vw - 20rem) / 80));
}
h4 {
  font-size: 22px;
  font-size: calc(1.375rem + 0.5 * ((100vw - 20rem) / 80));
}
h5 {
  font-size: 20px;
  font-size: calc(1.25rem + 0.5 * ((100vw - 20rem) / 80));
}
h6 {
  font-size: 18px;
  font-size: calc(1.125rem + 0.5 * ((100vw - 20rem) / 80));
}
p {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 1.25rem;
  font-size: calc(1rem + 0.25 * ((100vw - 20rem) / 80));
}
address {
  margin-bottom: 16px;
  font-style: normal;
  line-height: inherit;
}
dl {
  margin-top: 0;
  margin-bottom: 16px;
}
dt {
  font-weight: bold;
}
b,
strong {
  font-weight: bolder;
}
small {
  font-size: 80%;
}
pre,
code,
kbd,
samp {
  font-family: 'SFMono-Regular', Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
  font-size: inherit;
}
/*===================================================================
  GLOBAL Rules
=====================================================================*/
/* Main part from Reboot.css -
 * https://github.com/twbs/bootstrap/blob/v4-dev/scss/_reboot.scss
 * Also nice advices from -
 * https://justmarkup.com/log/2018/03/collection-of-css-snippets/
 */
*,
*:before,
*:after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
body {
  overflow-x: hidden /* To hide horizontal scroll from extra margin in row */;
  margin: 0;
  color: #000;
  background-color: #fff;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
}
img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  text-align: center;
  vertical-align: middle;
  word-break: break-all;
  color: #fff;
  background-color: #007bff;
  font-size: 20px;
  font-style: italic;
  line-height: 2.2;
}
img::before {
  content: 'Картинка (' attr(src) ') не загрузилась: ';
  padding-left: 32px;
}
img::after {
  content: '';
  padding-right: 32px;
}
audio,
video {
  width: 100%;
}
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}
ol,
ul {
  margin-top: 0;
  margin-bottom: 16px;
}
ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}
ol li,
ul li {
  margin-bottom: 8px;
}
abbr[title] {
  cursor: help;
  text-decoration: underline;
  border-bottom: 0;
}
pre {
  overflow: auto;
  margin-top: 0;
  margin-bottom: 16px;
}
figure {
  margin: 0 0 16px;
}
figcaption {
  text-align: center;
}
hr {
  margin-bottom: 32px;
}
/* Other global rules
=====================================================================*/
.noscript {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 50px;
  color: #ddd;
  background-color: rgba(40,46,52,0.9);
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.noscript-wrap {
  min-width: 320px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
  padding: 8px;
  text-align: center;
}
.noscript-link {
  color: #007bff;
}
.noscript-link:hover {
  text-decoration: none;
}
::selection {
  color: #000;
  background-color: #007bff;
  text-shadow: none;
}
.disabled,
[disabled],
[disabled] + span,
a.disabled,
a:visited.disabled {
  cursor: default;
  text-decoration: none;
  pointer-events: none;
  opacity: 0.7;
  color: #9babb4;
  border-color: #9babb4;
}
.disabled:hover,
[disabled]:hover,
[disabled] + span:hover,
a.disabled:hover,
a:visited.disabled:hover {
  color: #9babb4;
  background-color: #fff;
}
