}
.forms-selectGroup label,
.forms-checkboxGroup label,
.forms-radioGroup label,
.forms-html5InputsGroup label {
  order: -1;
  min-width: 150px;
  margin-right: 16px;
}
.forms-selectGroup input,
.forms-checkboxGroup input,
.forms-radioGroup input,
.forms-html5InputsGroup input,
.forms-selectGroup textarea,
.forms-checkboxGroup textarea,
.forms-radioGroup textarea,
.forms-html5InputsGroup textarea {
  color: #9babb4;
}
.forms-selectGroup input[type='radio'],
.forms-checkboxGroup input[type='radio'],
.forms-radioGroup input[type='radio'],
.forms-html5InputsGroup input[type='radio'],
.forms-selectGroup input[type='checkbox'],
.forms-checkboxGroup input[type='checkbox'],
.forms-radioGroup input[type='checkbox'],
.forms-html5InputsGroup input[type='checkbox'],
.forms-selectGroup input[type='color'],
.forms-checkboxGroup input[type='color'],
.forms-radioGroup input[type='color'],
.forms-html5InputsGroup input[type='color'],
.forms-selectGroup select + label,
.forms-checkboxGroup select + label,
.forms-radioGroup select + label,
.forms-html5InputsGroup select + label {
  flex-grow: 0;
  min-width: initial;
}
.forms-selectGroup [type='radio'] + label,
.forms-checkboxGroup [type='radio'] + label,
.forms-radioGroup [type='radio'] + label,
.forms-html5InputsGroup [type='radio'] + label,
.forms-selectGroup [type='checkbox'] + label,
.forms-checkboxGroup [type='checkbox'] + label,
.forms-radioGroup [type='checkbox'] + label,
.forms-html5InputsGroup [type='checkbox'] + label {
  order: 1;
  min-width: initial;
}
.forms-selectGroup input:focus,
.forms-checkboxGroup input:focus,
.forms-radioGroup input:focus,
.forms-html5InputsGroup input:focus,
.forms-selectGroup textarea:focus,
.forms-checkboxGroup textarea:focus,
.forms-radioGroup textarea:focus,
.forms-html5InputsGroup textarea:focus {
  color: #000;
}
.forms-inputsGroup {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 32px;
}
.forms-inputsGroup input,
.forms-inputsGroup textarea {
  width: 100%;
}
.forms-inputsGroup input + label,
.forms-inputsGroup textarea + label {
  position: absolute;
  top: -20px;
  left: 8px /* Must be same that padding left on input */;
  transition: opacity 0.3s ease /* Must be same that transition on input */;
  color: #9babb4;
  font-size: 14px;
}
.forms-inputsGroup input:focus + label,
.forms-inputsGroup textarea:focus + label {
  opacity: 1;
  color: #000;
}
