.grid-innerWrap {
  overflow-x: auto;
  background: repeating-linear-gradient(to right, #9babb4, #9babb4 8px, #e9ecef 8px, #e9ecef calc(8.333333% - 8px), #9babb4 calc(8.333333% - 8px), #9babb4 calc(8.333333%));
}
.grid-header {
  background-image: url("https://monosnap.com/image/WmqiwrBdYXwkcofOuiPp88gL1a91th");
}
.grid-row {
  display: flex;
  flex-wrap: wrap;
}
.grid-row > * {
  text-align: center;
  border: 1px solid;
  border-radius: 6px;
  background-color: rgba(0,0,0,0.3);
}
.grid-row--1 > * {
  flex-basis: calc(100% / 12 * 1 - 16px);
  margin: 0 8px 16px;
}
.grid-row--2 > * {
  flex-basis: calc(100% / 12 * 2 - 16px);
  margin: 0 8px 16px;
}
.grid-row--3 > * {
  flex-basis: calc(100% / 12 * 3 - 16px);
  margin: 0 8px 16px;
}
.grid-row--4 > * {
  flex-basis: calc(100% / 12 * 4 - 16px);
  margin: 0 8px 16px;
}
.grid-row--5 > * {
  flex-basis: calc(100% / 12 * 6 - 16px);
  margin: 0 8px 16px;
}
.grid-row--6 > * {
  flex-basis: calc(100% / 12 * 12 - 16px);
  margin: 0 8px 16px;
}
.grid-offset--1 {
  flex-basis: calc(100% / 12 * 11 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 1 + 8px);
}
.grid-offset--2 {
  flex-basis: calc(100% / 12 * 10 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 2 + 8px);
}
.grid-offset--3 {
  flex-basis: calc(100% / 12 * 9 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 3 + 8px);
}
.grid-offset--4 {
  flex-basis: calc(100% / 12 * 8 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 4 + 8px);
}
.grid-offset--5 {
  flex-basis: calc(100% / 12 * 7 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 5 + 8px);
}
.grid-offset--6 {
  flex-basis: calc(100% / 12 * 6 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 6 + 8px);
}
.grid-offset--7 {
  flex-basis: calc(100% / 12 * 5 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 7 + 8px);
}
.grid-offset--8 {
  flex-basis: calc(100% / 12 * 4 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 8 + 8px);
}
.grid-offset--9 {
  flex-basis: calc(100% / 12 * 3 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 9 + 8px);
}
.grid-offset--10 {
  flex-basis: calc(100% / 12 * 2 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 10 + 8px);
}
.grid-offset--11 {
  flex-basis: calc(100% / 12 * 1 - 16px);
  margin: 0 8px 16px;
  margin-left: calc(100% / 12 * 11 + 8px);
}
.embedded-header {
  background-image: url("https://monosnap.com/image/HrfN4chtfzFNuk9VkmupiXatWIpMTE");
}
.embedded-figureWrap,
.embedded-rest {
  display: flex;
  flex-wrap: wrap;
  margin-right: -8px;
  margin-left: -8px;
}
.embedded-figureWrap > * {
  flex-basis: calc(100% / 12 * 6 - 16px);
  margin: 0 8px 16px;
  min-width: 250px;
  flex-grow: 1;
}
.embedded-rest > * {
  flex-basis: calc(100% / 12 * 4 - 16px);
  margin: 0 8px 16px;
  min-width: 250px;
  flex-grow: 1;
}
@-moz-keyframes slide-right {
  0% {
    transform: skewX(0deg);
  }
  10% {
    transform: skewX(20deg) translateX(-50px);
  }
  100% {
    transform: translateX(2000px);
  }
}
@-webkit-keyframes slide-right {
  0% {
    transform: skewX(0deg);
  }
  10% {
    transform: skewX(20deg) translateX(-50px);
  }
  100% {
    transform: translateX(2000px);
  }
}
@-o-keyframes slide-right {
  0% {
    transform: skewX(0deg);
  }
  10% {
    transform: skewX(20deg) translateX(-50px);
  }
  100% {
    transform: translateX(2000px);
  }
}
@keyframes slide-right {
  0% {
    transform: skewX(0deg);
  }
  10% {
    transform: skewX(20deg) translateX(-50px);
  }
  100% {
    transform: translateX(2000px);
  }
}
@-moz-keyframes hideBg {
  0% {
    opacity: 1;
  }
  100% {
    top: -2000px;
    opacity: 0;
  }
}
@-webkit-keyframes hideBg {
  0% {
    opacity: 1;
  }
  100% {
    top: -2000px;
    opacity: 0;
  }
}
@-o-keyframes hideBg {
  0% {
    opacity: 1;
  }
  100% {
    top: -2000px;
    opacity: 0;
  }
}
@keyframes hideBg {
  0% {
    opacity: 1;
  }
  100% {
    top: -2000px;
    opacity: 0;
  }
}
/*===================================================================
  MEDIA QUERRIES
=====================================================================*/
@media screen and (max-width: 768px) {
  body {
    font-size: 14px;
    font-size: calc(14px + 0.375 * ((100vw - 20rem) / 80));
    line-height: 1.25;
  }
  table thead,
  table tbody,
  table tfoot,
  table tr,
  table th,
  table td,
  table caption {
    display: block;
  }
  table tr {
    margin-bottom: 16px;
  }
  table tr:nth-child(odd) {
    background-color: unset;
  }
  table th {
    display: none;
  }
  table td {
    position: relative;
    margin-top: -1px;
    padding-left: 37%;
    font-size: 12px;
    font-size: calc(12px + 0.5 * ((100vw - 20rem) / 80));
  }
  table td:nth-child(odd) {
    background-color: #eee;
  }
  table td::before {
    content: attr(data-label);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35%;
    height: 100%;
    padding: 0 4px;
    background-color: #007bff;
    text-align: center;
    color: #fff;
    font-weight: bold;
    line-height: 1;
  }
}
